<template>
	<div class="gCCamp">
		<gCShikigami v-for="(item,index) in camp" :shikigamiT="item"></gCShikigami>
	</div>
</template>

<script>
	import gCShikigami from './GCShikigami';
	
	export default{
		props:[
			'campT'
		],
		data(){
			return{
				camp:this.campT
			}
		},
		components:{
			gCShikigami
		},
		mounted() {
		}
	};
</script>

<style>
	.gCCamp{
		width: 40.5vw;
		height: 13.5vw;
		margin: auto;
		outline: #cfa5b1 2px solid;
		display: flex;
	}
</style>



